.Boxx-Image-1{
  position: relative;
  background: #fff;
  color: #64DD17;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: 400;
  letter-spacing: 0.1em;
  padding: 10px 30px 10px 30px;
  transition: 0.5s;
  height: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  min-height: 82px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Boxx-Image-1:hover {
  background: #64DD17;
  color: #64DD17;
  box-shadow: 0 0 35px var(--cr);
  cursor: pointer;
}
.Boxx-Image-1::before {
  content: "";
  position: absolute;
  inset: 2px;
  background: #212121;
}
.Boxx-Image-1:hover{
  color: red;
}
.Boxx-Image-1 .Boxx-Image-Text {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 1;
  color: rgb(255, 255, 255);
}

.Boxx-Image-1 .Boxx-Image-Help {
  position: absolute;
  inset: 0;
  display: block;
  
}

.Boxx-Image-1 .Boxx-Image-Help::before {
  content: "";
  position: absolute;
  top: 0;
  left: 80%;
  width: 10px;
  height: 4px;
  background: white;
  transform: translate(-50%) skew(325deg);
  transition: 0.5s;
}

.Boxx-Image-1:hover .Boxx-Image-Help::before {
  width: 20px;
  left: 20%;
}

.Boxx-Image-1 .Boxx-Image-Help::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 20%;
  width: 10px;
  height: 4px;
  background: white;
  transform: translate(-50%) skew(325deg);
  transition: 0.5s;
}

.Boxx-Image-1:hover .Boxx-Image-Help::after {
  width: 20px;
  left: 80%;
}

