.inputBox {
  position: relative;
  width: 250px;
}
.inputBox .inputt {
  width: 250px;
  padding: 10px;
  border: 1px solid rgb(255, 255, 255, 0.7);
  background: rgb(255, 255, 255);
  border-radius: 5px;
  outline: none;
  color: rgb(0, 0, 0);
  font-size: 1em;
  transition: 0.5s;
}

.inputBox .Rdomnoob {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 0px 5px 10px;
  position: absolute;
  bottom: 10px;
  pointer-events: none;
  font-size: 14px;
  letter-spacing: 0.2em;
  color: rgba(0, 0, 0, 0.7);
  transition: 0.5s;
}

.inputBox .inputt:focus ~ .Rdomnoob {
  color: #75e44f;
  transform: translateX(10px) translateY(-7px);
  font-size: 15px;
  padding: 0 10px;
  background: rgb(0, 0, 0);
  border-left: 1px solid #75e44f;
  border-right: 1px solid #75e44f;
  letter-spacing: 0.2em;
  margin-bottom: 20px;
}

.inputBox:nth-child(2) .inputt:focus ~ .Rdomnoob {
  background: #75e44f;
  color: rgb(0, 0, 0);
  border-radius: 2px;
}

.inputBox .inputt:focus {
  border: 1px solid #75e44f;
}
